import React from 'react'
import { Wizard, Steps, Step } from 'react-albus'
import { Link } from 'react-router-dom'
import { I18n } from 'react-i18nify-lite'

import permOne from '../images/onboarding/ilust/permissions-01.png'
import permTwo from '../images/onboarding/ilust/permissions-02.png'
import permThree from '../images/onboarding/ilust/permissions-03.png'
import permFour from '../images/onboarding/ilust/permissions-04.png'
import permFive from '../images/onboarding/ilust/permissions-05.png'

export default class Permissions extends React.Component {

  constructor(props){
    super(props)
    this.state = {
      data: {}
    }
  }

  openCustomURLinIFrame = (src)=> {
      var rootElm = document.documentElement;
      var newFrameElm = document.createElement("IFRAME");
      newFrameElm.setAttribute("src",src);
      rootElm.appendChild(newFrameElm);
      //remove the frame now
      newFrameElm.parentNode.removeChild(newFrameElm);
  }

  render() {
    return <div className="perms fs-onboarding regular inverted">
    <div className="column">
      <h4 className="heading">{I18n.t("iospermissions.title")}</h4>
      <figure className="bg-fig">
        <i className="icon-key-double"/>
      </figure>
      <Wizard>
        <Steps>
          <Step
            id="location"
            render={({ next }) => (
              <div className="step">
                <p>{I18n.t("iospermissions.location")}
                <br />{I18n.t("iospermissions.location2")} <b>{I18n.t("iospermissions.location3")}</b>.</p>
                <figure>
                  <img src={permOne} className="first" alt="permOne" />
                </figure>
                <button className="btn cta l success icon-right" onClick={(e) => this.openCustomURLinIFrame("iosauthlocation://")}>Ok</button>
                <button id="btnLocation" className="hidden" onClick={next}>Ok</button>
              </div>
            )}
          />
          <Step
            id="photos"
            render={({ next }) => (
              <div className="step">
                <p>{I18n.t("iospermissions.photos")}</p>
                <figure>
                  <img src={permTwo} className="first" alt="permTwo" />
                </figure>
                <button className="btn cta l success icon-right" onClick={(e) => this.openCustomURLinIFrame("iosauthphotos://")}>Ok</button>
                <button id="btnPhotos" className="hidden" onClick={next}>Ok</button>
              </div>
            )}
          />
          <Step
            id="contacts"
            render={({ next }) => (
              <div className="step">
                <p>{I18n.t("iospermissions.contacts")}</p>
                <figure>
                  <img src={permThree} className="first" alt="permThree" />
                </figure>
                <button className="btn cta l success icon-right" onClick={(e) => this.openCustomURLinIFrame("iosauthcontacts://")}>Ok</button>
                <button id="btnContacts" className="hidden" onClick={next}>Ok</button>
              </div>
            )}
          />
          <Step
            id="camera"
            render={({ next }) => (
              <div className="step">
                <p>{I18n.t("iospermissions.camera")}</p>
                <figure>
                  <img src={permFour} className="first" alt="permFour" />
                </figure>
                <button className="btn cta l success icon-right" onClick={(e) => this.openCustomURLinIFrame("iosauthcamera://")}>Ok</button>
                <button id="btnCamera" className="hidden" onClick={next}>Ok</button>
              </div>
            )}
          />
          <Step
            id="notifications"
            render={({ next }) => (
              <div className="step">
                <p>{I18n.t("iospermissions.notification")}</p>
                <figure>
                  <img src={permFive} className="first" alt="permFive" />
                </figure>
                <button className="btn cta l success icon-thumb-up" onClick={(e) => this.openCustomURLinIFrame("iosauthnotification://")}>Ok</button>
              </div>
            )}
          />
        </Steps>
      </Wizard>
    </div>
  </div>
  }
}
